import { useEffect, useState } from 'react';
import IconButton from '../iconButton';
import classes from './styles.module.css';
import DarkModeIcon from '../../assets/darkModeIcon';
import LightModeIcon from '../../assets/lightModeIcon';
import { themes, getTheme, setTheme } from '../../utils/theme';


const Page = props => {
    const [darkMode, setDarkMode] = useState(false);

    useEffect(() => loadTheme(), []);

    const loadTheme = () => {
        const currTheme = getTheme();
        setDarkMode(currTheme === themes.DARK);
        setTheme(currTheme);
    };
    const handleThemeChange = () => {
        const currTheme = getTheme();
        setDarkMode(currTheme !== themes.DARK );
        setTheme(currTheme === themes.DARK ? themes.LIGHT : themes.DARK);
        if (typeof props.shouldComponentUpdate === 'function') props.shouldComponentUpdate(new Date().getTime());
    };

    return <div className={classes.page}>
        <div className={classes.pageBackground} />
        <div className={classes.pageContent}>
            <IconButton onClick={handleThemeChange}
                style={{
                    display: 'flex',
                    alignItems: 'center',
                    position: 'absolute',
                    top: '16px',
                    right: '16px',
                }}>
                {darkMode ? <DarkModeIcon /> : <LightModeIcon />}
            </IconButton>
            {props.children}
        </div>
    </div>;
};

export default Page;